<template>
  <div class="game-wrap">
    <h1 class="game__title">Simon</h1>
    <div class="game-content">
      <div class="game-left-side">
        <svg
          id="simon"
          xmlns="http://www.w3.org/2000/svg"
          xmlns:xlink="http://www.w3.org/1999/xlink"
          width="600"
          height="600"
          viewBox="0 0 600 600"
        >
          <g>
            <image
              id="simon_bg"
              data-name="simon_bg"
              width="600"
              height="600"
              xlink:href="data:img/png;base64,"
            />
            <image
              @click="addClick"
              id="green"
              class="simon__part"
              data-id="0"
              x="26"
              y="26"
              width="249"
              height="249"
              xlink:href="data:img/png;base64,"
            />
            <image
              @click="addClick"
              id="red"
              class="simon__part"
              data-id="1"
              x="325"
              y="26"
              width="249"
              height="249"
              xlink:href="data:img/png;base64,"
            />
            <image
              @click="addClick"
              id="blue"
              class="simon__part"
              data-id="2"
              x="325"
              y="325"
              width="249"
              height="249"
              xlink:href="data:img/png;base64,"
            />
            <image
              @click="addClick"
              id="yellow"
              class="simon__part"
              data-id="3"
              x="26"
              y="325"
              width="249"
              height="249"
              xlink:href="data:img/png;base64,"
            />
          </g>
          <text
            x="50%"
            y="51%"
            id="message"
            text-anchor="middle"
            fill="#2ae15a"
            font-weight="bold"
            font-size="20px"
            font-family="Arial"
          ></text>
        </svg>
      </div>
      <div class="game-right-side">
        <p class="game__round">
          Round: <span>{{ round }}</span>
        </p>
        <button
          @click="start"
          class="game__btn"
          :disabled="playGame"
          v-if="!playGame"
        >
          Start Game
        </button>
        <ul class="game-difficulty" v-if="!playGame">
          <p>Difficulty level:</p>
          <li class="game-difficulty__item">
            <input type="radio" id="light" value="light" v-model="difficulty" />
            <label for="light">Light</label>
          </li>
          <li class="game-difficulty__item">
            <input
              type="radio"
              id="middle"
              value="middle"
              v-model="difficulty"
            />
            <label for="middle">Middle</label>
          </li>
          <li class="game-difficulty__item">
            <input type="radio" id="hard" value="hard" v-model="difficulty" />
            <label for="hard">Hard</label>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Simon",
  data() {
    return {
      difficulty: "light",
      round: 0,
      sequence: [],
      playGame: false,
      playSeq: false,
      count: 0,
    };
  },
  computed: {
    checkDifficulty() {
      if (this.difficulty === "middle") return 1000;
      if (this.difficulty === "hard") return 400;
      return 1500;
    },
  },
  methods: {
    start() {
      this.playGame = true;
      this.getSequence();
    },
    getSequence() {
      this.round++;
      this.sequence = Array.from({ length: this.round }, () =>
        Math.floor(Math.random() * (5 - 1))
      );
      this.playSequence();
    },
    playSequence() {
      this.playSeq = true;
      console.log(this.sequence);
      const parts = document.querySelectorAll("image.simon__part");
      let i = 0;

      const hide = (i) => {
        setTimeout(() => {
          parts[i].classList.remove("simon__part--active");
        }, 200);
      };

      const show = () => {
        parts[this.sequence[i]].classList.add("simon__part--active");
        this.playSound(this.sequence[i]);
        hide(this.sequence[i]);
        i++;
        if (i < this.sequence.length) {
          setTimeout(show, this.checkDifficulty);
        } else {
          this.playSeq = false;
        }
      };
      show();
    },
    playSound(i) {
      const audio = new Audio();
      audio.src = require(`@/assets/sounds/${i + 1}.mp3`);
      audio.autoplay = true;
    },
    addClick(e) {
      if (this.playGame && !this.playSeq) {
        const partId = +e.target.dataset.id;
        this.playSound(partId);
        this.checkSequence(partId);
      }
    },
    checkSequence(partId) {
      const num = this.sequence.shift();
      if (partId !== num) {
        this.message("error");
        this.playGame = false;
        this.round = 0;
      } else if (this.sequence.length <= 0) {
        this.message("correctly");
        const timer = setTimeout(() => {
          this.getSequence();
          clearTimeout(timer);
        }, 1500);
      }
    },
    message(result) {
      const message = document.querySelector("#message");
      if (result === "error") {
        message.style.fill = "red";
        message.textContent = `I'm sorry, but you did not press correctly`;
      } else if (result === "correctly") {
        message.style.fill = "green";
        message.textContent = `Excellent!!! Let's continue :)`;
      }
      const timer = setTimeout(() => {
        message.textContent = "";
        clearTimeout(timer);
      }, 1500);
    },
  },
};
</script>

<style scoped>
.game-wrap {
  max-width: 1140px;
  margin: auto;
}
.game-content {
  display: flex;
  justify-content: space-around;
}

.game__btn {
  background: #6dabe8;
  border: none;
  border-radius: 10px;
  padding: 15px 20px;
  color: #ffffff;
  cursor: pointer;
}

.game-difficulty {
  list-style: none;
  padding: 0;
}

.game-difficulty__item {
  display: flex;
  margin-bottom: 10px;
}

.game-difficulty__item input {
  margin-right: 10px;
}

.simon__part {
  opacity: 0.5;
  transition: opacity 0.3s;
  cursor: pointer;
}
.simon__part:hover,
.simon__part--active {
  opacity: 1;
}
</style>